import React from 'react';
import './Projects.css';

function Projects() {
  return (
    <div className="projects-container">
      <h1 className="projects-title">Projects</h1>

      <div className="project-item">
        <h2 className="project-title">1. Carbon Sequestration Prediction</h2>
        <p className="project-date"><strong>Duration:</strong> May 2024 to July 2024</p>
        <p className="project-description">
          This self-initiated full-stack project aims to predict the carbon sequestration potential of trees. 
          It encompasses end-to-end development, from data processing to user interface design:
        </p>
        <ul className="project-details">
          <li><strong>Backend Implementation:</strong> Developed using Python and Flask. Integrated a RandomForestRegressor model to predict carbon content based on tree attributes.</li>
          <li><strong>Frontend Development:</strong> Created a responsive user interface with React.js, allowing users to input tree data and view predictions.</li>
          <li><strong>Database Integration:</strong> Utilized SQL Server for data storage and management, ensuring efficient data retrieval and updates.</li>
        </ul>
        <p className="project-achievements">
          This project demonstrated my ability to handle complex data processing tasks and integrate machine learning models with modern web technologies.
        </p>
      </div>

      <div className="project-item">
        <h2 className="project-title">2. Multi-Stream Feature Fusion for Traffic Prediction</h2>
        <p className="project-date"><strong>Duration:</strong> May 2023 - July 2023</p>
        <p className="project-description">
          An academic project focusing on improving traffic prediction accuracy by leveraging multiple data streams and advanced machine learning techniques:
        </p>
        <ul className="project-details">
          <li><strong>Data Collection:</strong> Aggregated data from various sources, including traffic sensors and historical records.</li>
          <li><strong>Feature Engineering:</strong> Applied feature fusion to merge data streams and enhance the predictive power of the model.</li>
          <li><strong>Model Development:</strong> Tested and refined machine learning algorithms to select the most accurate model for traffic forecasting.</li>
          <li><strong>Performance Evaluation:</strong> Evaluated model performance using metrics such as accuracy, precision, and recall, and made improvements based on results.</li>
        </ul>
        <p className="project-achievements">
          This project provided hands-on experience with feature engineering and machine learning model optimization, contributing to better traffic management solutions.
        </p>
      </div>

      <div className="project-item">
        <h2 className="project-title">3. CRUD Operations Using IntelliJ and ReactJS for Full Stack Development</h2>
        <p className="project-date"><strong>Duration:</strong> January 2024 - March 2024</p>
        <p className="project-description">
          Completed with the help of YouTube tutorials, this project involved building a CRUD (Create, Read, Update, Delete) application:
        </p>
        <ul className="project-details">
          <li><strong>Frontend Development:</strong> Developed the user interface using ReactJS, allowing users to perform CRUD operations with an intuitive design.</li>
          <li><strong>Backend Development:</strong> Implemented the backend with Java and IntelliJ, creating a RESTful API to handle data transactions.</li>
          <li><strong>Database Integration:</strong> Used MySQL for data storage and management, ensuring data consistency and reliability.</li>
          <li><strong>Testing and Deployment:</strong> Conducted thorough testing to ensure application functionality and deployed it on a web server for user access.</li>
        </ul>
        <p className="project-achievements">
          This project solidified my understanding of full-stack development and demonstrated my ability to build functional applications from scratch using available resources.
        </p>
      </div>
    </div>
  );
}

export default Projects;
