import React, { useState } from 'react';

function Home() {
  const [isZoomed, setIsZoomed] = useState(false);

  const handleImageClick = () => {
    setIsZoomed(prevIsZoomed => !prevIsZoomed);
  };

  return (
    <div className="container" style={containerStyle}>
      <div style={imageContainerStyle}>
        <div 
          style={{ 
            ...imageWrapperStyle, 
            transform: isZoomed ? 'scale(2)' : 'scale(1)', 
            cursor: 'pointer' 
          }} 
          onClick={handleImageClick}
        >
          <img 
            src="C:\Users\rashm\portfolio-ai-assistant\public\images\Rashmi.jpeg" // Ensure this path is correct
            alt="Rashmi" 
            style={imageStyle} 
          />
        </div>
      </div>
      <div style={textContainerStyle}>
        <h1>Welcome to My Portfolio</h1>
        <p>Hi, I'm Rashmi, a software developer with a passion for AI and full-stack development.</p>
        <p>I am currently seeking an entry-level software developer role where I can apply my skills and contribute to exciting projects.</p>
      </div>
    </div>
  );
}

const containerStyle = {
  display: 'flex',
  alignItems: 'center',
  padding: '20px',
};

const imageContainerStyle = {
  marginRight: '20px',
  textAlign: 'center',
};

const imageWrapperStyle = {
  width: '200px',
  height: '200px',
  overflow: 'hidden',
  borderRadius: '50%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  position: 'relative',
  transition: 'transform 0.3s',
};

const imageStyle = {
  width: 'auto',
  height: '100%',
  position: 'absolute',
  top: '-20px', // Adjust this value to control the vertical position of the face
};

const textContainerStyle = {
  maxWidth: '600px',
};

export default Home;
